<script>
export default {
  name: 'App'
}
</script>

<style>
#app {
  --theme-primary-color: rgb(33, 150, 243);
  --theme-secondary-color: white;
  background: url("/static/img/bg/possiblebg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}

.w3-content {
  max-width: 1200px;
}

